@import url(https://fonts.googleapis.com/css2?family=Festive&family=Tourney&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.container::after {
  content: "";
  background: url(/star-wars-people/static/media/yodaback.89b97566.jpg);
  background-repeat: no-repeat;
  background-position: center;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  opacity: 0.4;
  z-index: -1;
}

.container .top {
  height: 70px;
  display: flex;
  flex-direction: row;
  align-items: center;
  padding-left: 10px;
}

.top h1 {
  font-size: 2rem;
  font-family: "Tourney", cursive;
  color: #c0392b;
}

.container .body {
  flex: 1 1;
  display: flex;
  flex-direction: row;
  padding: 0px 20px;
}

.container .body section {
  flex: 1 1;
  display: flex;
  flex-direction: column;
}

.listing {
  list-style: none;
  padding-left: 50px;
}

.listing li {
  margin: 5px;
}

section .title {
  text-align: center;
}

section .title h1 {
  text-transform: capitalize;
  font-size: 2rem;
  color: #5c8001;
  border-bottom: 3px solid #c0392b;
  display: inline;
  padding: 4px;
  font-family: monospace;
}

section .detail {
  text-align: left;
  text-transform: capitalize;
  padding: 20px 50px;
  height: 700px;
  overflow-y: scroll;
}

section .detail p {
  font-family: monospace;
}

section .detail div {
  border-bottom: 2px solid #e3e4e677;
  margin-bottom: 10px;
  font-size: 1rem;
  color: #2c2c54;
}

section .detail div span {
  font-family: monospace;
  font-size: 1.1rem;
  font-weight: bold;
  color: #c0392b;
}

aside * {
  border: 0px;
}

.container .body aside {
  width: 400px;
  padding: 10px;
}

.body aside ul {
  display: flex;
  flex-direction: column;
  overflow-y: scroll;
  height: 780px;
  padding: 0 10px;
}

.body aside li {
  padding: 7px;
  box-shadow: 2px 2px 4px -3px;
  margin-bottom: 6px;
  cursor: pointer;
}
.body aside li:hover {
  background-color: #dedede;
}
.body aside li:hover h3 {
  color: #3b5300;
}

.body aside li:hover p {
  color: #b33939;
}

aside li h3 {
  font-size: 1.4rem;
  color: #718093;
  margin-bottom: 4px;
}

aside li p {
  font-size: 1rem;
  color: #718093;
}

.control-btn {
  border-top: 2px solid #5c8001;
  margin-top: auto;
  margin-bottom: 10px;
  text-align: center;
  padding: 5px;
}

.control-btn button {
  font-size: 1rem;
  padding: 0px 15px;
  background-color: #c0392b;
  border: 0px;
}

.control-btn button:hover {
  background-color: #e05848;
}

